// Import the page"s CSS. Webpack will know what to do with it.
import "../styles/app.css";

// Import libraries we need.
import { default as Web3 } from "web3";
import { default as contract } from "truffle-contract";
import $ from "jquery";

// Import our contract artifacts and turn them into usable abstractions.
import ticTacToeArtifact from "../../build/contracts/TicTacToe.json";

// TicTacToe is our usable abstraction, which we'll use through the code below.
const TicTacToe = contract(ticTacToeArtifact);

// Current eth price (e.g. US$ per ether)
const ETH_PRICE = 235.9933;

// The following code is simple to show off interacting with your contracts.
// As your needs grow you will likely need to change its form and structure.
// For application bootstrapping, check out window.addEventListener below.
let accounts;
let account;
var ticTacToeInstance;

// Events
var nextPlayerEvent;
var winEvent;
var drawEvent;
var timeoutEvent;

// Prior values
var startBalanceWei;
var startMoveBalanceWei;

const calcUsDollarsForWei = (weiAmount) => ETH_PRICE * (weiAmount / 1000000000000000000);

const App = {
  start: function () {
    // Bootstrap the TicTaxToe abstraction for Use.
    TicTacToe.setProvider(web3.currentProvider);

    // Get the initial account balance so it can be displayed.
    web3.eth.getAccounts(function (err, accs) {
      if (err != null) {
        alert("There was an error fetching your accounts.");
        return;
      }

      if (accs.length === 0) {
        alert("Couldn't get any accounts! Make sure your Ethereum client is configured correctly.");
        return;
      }

      accounts = accs;
      account = accounts[0];

      App.updateStatus("Waiting for You to Create or Join a Game...", "#ccccff");
    });
  },
  useAccountOne: function () {
    account = accounts[1];
  },
  createNewGame: function () {
    console.log("Create game called");

    // Clear old values
    startBalanceWei = null;
    startMoveBalanceWei = null;

    // Establish new values
    App.showAccountValue();

    TicTacToe.new({ from:account, value:web3.toWei(0.1, "ether"), gas:3000000 }).then(instance => {
      ticTacToeInstance = instance;

      // Update GUI controls
      $(".in-game").show();
      $(".waiting-for-join").hide();
      $(".game-start").hide();

      // Watch for second player joining game and enable cell onclick handler at that point
      var playerJoinedEvent = ticTacToeInstance.PlayerJoined();
      playerJoinedEvent.watch(function (error, eventObj) {
        // playerJoinedEvent.stopWatching();
        $(".waiting-for-join").show();
        $("#opponent-address")[0].innerHTML = eventObj.args.playerAddress;

        if (!error) {
          console.log("Second player joined", eventObj);
        } else {
          console.error(error);
        }
      });

      App.listenForEvents();

      $("#game-address")[0].innerHTML = ticTacToeInstance.contract.address;
      $("#game-address-copyable").val(ticTacToeInstance.contract.address);
      // $("#status")[0].innerHTML = "Waiting for Second Player to Join";
      App.updateStatus("Waiting for Second Player to Join", "yellow");

      App.showAccountValue();

      console.log("TicTacToe instance created", ticTacToeInstance);
    }).catch(error => {
      console.log("Error creating TicTacToe instance", error);
    });
  },
  joinGame: function () {
    console.log("joinGame called");
    var gameAddress = prompt("Address of the Game");
    if (gameAddress != null) {
      TicTacToe.at(gameAddress).then(instance => {
        ticTacToeInstance = instance;

        // Update GUI controls
        $(".in-game").show();
        $(".game-start").hide();
        $(".waiting-for-join").show();
        ticTacToeInstance.player1.call().then(player1Address => {
          $("#opponent-address")[0].innerHTML = player1Address;
        });

        App.listenForEvents();

        $("#game-address")[0].innerHTML = ticTacToeInstance.contract.address;
        // $("#status")[0].innerHTML = "Waiting for Game to Start";
        App.updateStatus("Waiting for Game to Start", "yellow");

        // Clear old values
        startBalanceWei = null;
        startMoveBalanceWei = null;

        // Establish new values
        App.showAccountValue();

        return ticTacToeInstance.joinGame({ from:account, value:web3.toWei(0.1, "ether"), gas:3000000 });
      }).then(txResult => {
        console.log("Joined game", txResult);
      });
    }
  },
  setStone: function (event) {
    console.log("setStone event", event);

    // Remove the onclick handler from the board cells
    for (var col = 0; col < 3; col++) {
      for (var row = 0; row < 3; row++) {
        $($("#board")[0].children[0].children[col].children[row]).prop("onclick", null).off("click");
      }
    }

    // $("#status")[0].innerHTML = "Move accepted - waiting for mining to finalize";
    App.updateStatus("Move accepted - waiting for mining to finalize", "#ccccff");
    $($("#board")[0].children[0].children[event.data.x].children[event.data.y]).css("background-color", "#77ff77");

    ticTacToeInstance.setStone(event.data.x, event.data.y, { from: account }).then(txResult => {
      console.log("Click result", txResult);
      App.printBoard();
    }).catch(err => {
      console.log("Error setting stone:", err);
      // $("#status")[0].innerHTML = "Error setting stone (this has been logged)";
      App.updateStatus("Error setting stone (this has been logged)", "#ffcccc");
      ticTacToeInstance.emergencyCashout({ from: account }).then(txResult => {
        console.log("Error during gameplay - emergency cashout:", txResult);
      }).catch(err => {
        console.log("Emergency cashout error:", err);
      });
    });
  },
  printBoard: function () {
    return new Promise(function (resolve, reject) {
      ticTacToeInstance.getBoard.call().then(board => {
        console.log("print board:", board);
        for (var row = 0; row < board.length; row++) {
          for (var col = 0; col < board[row].length; col++) {
            $($("#board")[0].children[0].children[row].children[col]).css("background-color", "#ffffff");
            if (board[row][col] === account) {
              // Draw X
              $("#board")[0].children[0].children[row].children[col].innerHTML = "X";
            } else if (Number(board[row][col]) !== 0) {
              $("#board")[0].children[0].children[row].children[col].innerHTML = "O";
            } else {
              // Explicitly clear an unassigned cell - useful when another game is started
              // Alternatively, could add a loop to clear the board in the createNewGame function
              $("#board")[0].children[0].children[row].children[col].innerHTML = "";
            }
          }
        }
        resolve("Board updated");
      });
    });
  },
  nextPlayer: function (error, eventObj) {
    console.log("nextPlayer called:", eventObj);
    if (error) {
      console.log("nextPlayer error:", error);
    }

    App.printBoard().then(status => {
      if (eventObj.args.playerAddress === account) {
        // Our turn
        // Setup the onclick handler for detecting cell selections in the table
        for (var col = 0; col < 3; col++) {
          for (var row = 0; row < 3; row++) {
            console.log(`innerHTML for row:${row} col:${col} is`,
              $("#board")[0].children[0].children[row].children[col].innerHTML);
            if ($("#board")[0].children[0].children[col].children[row].innerHTML === "") {
              $($("#board")[0].children[0].children[col].children[row])
                .off("click").click({ x: col, y:row }, App.setStone);
            }
          }
        }
        // $("#status")[0].innerHTML = "Your turn";
        App.updateStatus("Your turn", "#ccffcc");
        App.showAccountValue();
      } else {
        // Opponent's turn
        // $("#status")[0].innerHTML = "Opponent's turn";
        App.updateStatus("Opponent's turn", "#ffffff");
        App.showAccountValue();
      }
    });
  },
  win: function (error, eventObj) {
    if (error) {
      console.log("win error:", error);
    }

    App.stopListeningForEvents();
    $(".leave-game").show();

    App.printBoard().then(status => {
      console.log("Game won:", eventObj);
      App.showAccountValue();
      if (eventObj.args.playerAddress === account) {
        // $("#status")[0].innerHTML = "You won!";
        App.updateStatus("You won! Congratulations!", "#ccffcc");
        alert("Game over. You Won! Congratulations!");
      } else {
        // $("#status")[0].innerHTML = "You lost!";
        App.updateStatus("Unfortunately you lost", "#ffffff");
        alert("Game over. You lost. Better luck next time.");
      }
    });
  },
  draw: function (error, eventObj) {
    if (error) {
      console.log("Draw error:", error);
    }

    App.stopListeningForEvents();
    $(".leave-game").show();

    App.printBoard().then(status => {
      console.log("Game draw:", eventObj);
      App.showAccountValue();
      // $("#status")[0].innerHTML = "Game ended in a draw";
      App.updateStatus("Game ended in a draw", "yellow");
      alert("Game over. It ended in a draw.");
    });
  },
  timeout: function (error, eventObj) {
    console.log("Game timeout:", eventObj);
    if (error) {
      console.log("Game timeout error:", error);
    }

    // $("#status")[0].innerHTML = "Game timed-out";
    App.updateStatus("Game time-out. Player took too long to move", "#ffcccc");
    alert("Game timed-out. Player took to long to make a move - will result in a draw");
  },
  listenForEvents: function () {
    // Watch for the NextPlayer event and call App.nextPlayer to handle it
    nextPlayerEvent = ticTacToeInstance.NextPlayer();
    nextPlayerEvent.watch(App.nextPlayer);

    // Watch for the GameOverWithWin event and call App.win to handle it
    winEvent = ticTacToeInstance.GameOverWithWin();
    winEvent.watch(App.win);

    // Watch for the GameOverWithDraw event and call App.draw to handle it
    drawEvent = ticTacToeInstance.GameOverWithDraw();
    drawEvent.watch(App.draw);

    // Watch for the GameTimeout event and call App.timeout to handle it
    timeoutEvent = ticTacToeInstance.GameTimeout();
    timeoutEvent.watch(App.timeout);
  },
  stopListeningForEvents: function () {
    // nextPlayerEvent.stopWatching();
    // winEvent.stopWatching();
    // drawEvent.stopWatching();
    // timeoutEvent.stopWatching();
  },
  showAccountValue: function () {
    web3.eth.getBalance(account, function (err, res) {
      if (err) {
        return console.log("Error getting balance:", err);
      }
      $("#account-value")[0].innerHTML = Number(res).toLocaleString() + " wei";
      $("#account-value-usdollars")[0].innerHTML = "US$ " + calcUsDollarsForWei(Number(res)).toLocaleString();

      if (!startBalanceWei) {
        startBalanceWei = Number(res);
        startMoveBalanceWei = startBalanceWei;
      } else {
        $("#account-spend-on-move")[0].innerHTML = (startMoveBalanceWei - Number(res)).toLocaleString() +
          " wei (US$ " + calcUsDollarsForWei(startMoveBalanceWei - Number(res)).toLocaleString() + ")";
        $("#account-spend-on-game")[0].innerHTML = (startBalanceWei - Number(res)).toLocaleString() +
          " wei (US$ " + calcUsDollarsForWei(startBalanceWei - Number(res)).toLocaleString() + ")";
        startMoveBalanceWei = Number(res);
      }
    });
  },
  resetForNewGame: function () {
    for (var row = 0; row < 3; row++) {
      for (var col = 0; col < 3; col++) {
        $($("#board")[0].children[0].children[row].children[col]).css("background-color", "#ffffff");
        $("#board")[0].children[0].children[row].children[col].innerHTML = "";
      }
    }
    $(".in-game").hide();
    $(".game-start").show();
    $(".leave-game").hide();
    // $("#status")[0].innerHTML = "Waiting for You to Create or Join a Game";
    App.updateStatus("Waiting for You to Create or Join a Game", "#ccccff");
  },
  updateStatus: function (message, color) {
    if (!color) {
      $($("#status")[0]).css("background-color", "white");
    } else {
      $($("#status")[0]).css("background-color", color);
    }

    $("#status")[0].innerHTML = message;

    // $($("#board")[0].children[0].children[row].children[col]).css("background-color", "#ffffff");
    // $("#board")[0].children[0].children[row].children[col].innerHTML = "";
  },
  useLocalProvider: function (port) {
    console.log(`Setting local provider on port ${port}`);
    window.web3 = new Web3(new Web3.providers.HttpProvider(`http://127.0.0.1:${port}`));
    App.start();
  }
};

window.App = App;

