// Updated "secure" form of dapp provider interaction - browser requests provider
// rather than provider being automatically injected
// See article:
// https://medium.com/metamask/https-medium-com-metamask-breaking-change-injecting-web3-7722797916a8
window.addEventListener("load", () => {
  // If web3 is not injected (modern browsers)...
  if (typeof web3 === "undefined") {
    // Listen for provider injection
    window.addEventListener("message", ({ data }) => {
      if (data && data.type && data.type === "ETHEREUM_PROVIDER_SUCCESS") {
        // Use injected provider, start dapp...
        web3 = new Web3(ethereum);
        App.start();
      }
    });
    // Request provider
    window.postMessage({ type: "ETHEREUM_PROVIDER_REQUEST" }, "*");
  } else {
    // If web3 is injected (legacy browsers)...
    // Use injected provider, start dapp
    console.warn("Warning: the web3 provider was automatically injected." +
      " This is a security vulnerability. Be sure to update your dapp" +
      " browser or plug-in with an updated version that requires the user" +
      " approve access to the provider.");
    web3 = new Web3(web3.currentProvider);
    App.start();
  }
});
